<template>
  <div class="user-edit-container">
    <!-- Header 区域 -->
    <van-nav-bar title="编辑资料" left-arrow @click-left="$router.back()" fixed class="page-nav-bar" :border="false" />

    <!-- 用户资料 -->
    <van-cell-group class="action-card">
      <van-cell title="头像" is-link center>
        <template #default>
          <!-- <label for="file"><van-image round class="avatar" :src="profileObj.photo" /></label>
          <input type="file" style="display: none" id="file" /> -->
          <van-image round class="avatar" :src="$store.state.avatar" @click="$refs.iptFile.click()" />
          <!-- file 选择框 -->
          <input type="file" ref="iptFile" v-show="false" accept="image/*" @change="onFileChange" />
        </template>
      </van-cell>
      <van-cell title="名称" is-link :value="profileObj.name" @click="editNameFn" />
      <van-cell title="生日" is-link :value="profileObj.birthday" @click="editBirthday" />
    </van-cell-group>

    <!-- 修改姓名弹窗 -->
    <van-dialog v-model="show" title="标题" show-cancel-button :beforeClose="beforeCloseFn">
      <template #default>
        <input type="text" v-model="editName" v-fofo />
      </template>
    </van-dialog>
    <!-- 修改生日弹窗 -->
    <van-popup v-model="birShow" position="bottom" round :style="{ height: '50%' }" get-container=".user-edit-container">
      <van-datetime-picker v-model="currentDate" type="date" title="选择年月日" :min-date="minDate" :max-date="maxDate" @cancel="birShow = false" @confirm="confirmFn" />
    </van-popup>
  </div>
</template>

<script>
import { getUsermsg, editPhoto, editUsermsg } from '@/api/user'
import { formatDate } from '@/utils/dayjs'
export default {
  name: 'UserEdit',
  data() {
    return {
      profileObj: {},
      show: false, // 修改名字
      editName: '',
      birShow: false, // 修改日期
      minDate: new Date(1922, 0, 1), // 最小范围
      maxDate: new Date(), // 最大范围 默认获取系统日期
      currentDate: new Date(2022, 4, 26) // 当前时间
    }
  },
  async created() {
    const { data: res } = await getUsermsg()
    console.log(res)
    this.profileObj = res.data
  },
  methods: {
    // 文件选择改变
    async onFileChange(e) {
      // console.log(e.target.files[0]) // 数组是一个特殊对象
      if (e.target.files.length === 0) return
      const photo = new FormData()
      photo.append('photo', e.target.files[0])
      const { data: res } = await editPhoto(photo)
      this.$store.commit('setAvatar', res.data.photo)
      // console.log(res)
    },
    // 弹出层显示（姓名）
    editNameFn() {
      this.show = true
      this.editName = this.profileObj.name
    },
    // 弹出框关闭前函数(姓名)
    async beforeCloseFn(action, done) {
      if (action === 'confirm') {
        // 点确定 正则校验
        const reg = /^[A-Za-z0-9\u4e00-\u9fa5]{1,7}$/
        if (reg.test(this.editName)) {
          // 通过了校验
          this.profileObj.name = this.editName
          this.$store.commit('SETUNAME', this.profileObj.name)
          await editUsermsg({ name: this.editName })
          this.$toast('修改成功')
          // console.log(res)
          done()
        } else {
          this.$notify({ type: 'warning', message: '用户名格式有误，只能是中英文数字混合1到7位' })
          done(false)
        }
      } else {
        // 点取消
        done()
      }
    },
    // 生日弹层点击事件
    async editBirthday() {
      this.birShow = true
      this.currentDate = new Date(this.profileObj.birthday)
    },
    // 日期选择器完成事件
    async confirmFn() {
      // 日期选择器组件是currentDate日期对象，后端需要年-月-日格式
      const { data: res } = await editUsermsg({
        birthday: formatDate(this.currentDate)
      })
      this.profileObj.birthday = formatDate(this.currentDate)
      this.birShow = false
      this.$toast('修改成功')
      console.log(res)
    }
  }
}
</script>

<style lang="less" scoped>
.user-edit-container {
  padding-top: 96px;
  .avatar {
    width: 50px;
    height: 50px;
  }
  /deep/.van-dialog__content {
    text-align: center;
    input {
      border: 0;
      text-align: center;
    }
  }
}
</style>
